*, *::after, *::before {
  padding: 0;
  margin: 0;
  overflow: hidden;
}

body {
  background: black;
}

#canvas {
  display: block;
  width: 100%;
  height: 100vh;
}

.trinity {
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  transform: translate(-50%, -50%) scale(0);
  animation: scaleUp 200s linear;
  animation-fill-mode: forwards;

  &__img {
    width: 100%;
  }

}

@keyframes scaleUp {
  from {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.05);
  }

  to {
    opacity: 0.5;
    transform: translate(-50%, -50%) scale(1);
  }
}
